import Vue from 'vue'
import Vuex from 'vuex'
import AuthService from '@/services/UserAuthService'

/* GUARDA LOS DATOS DEL JSON LOCAL EN UNA VARIABLE */
const user = JSON.parse(localStorage.getItem('user'));
let store_user = []
if (user){
  if(!user.is_staff)
    store_user = user.stores ? user.stores[0].store : []
}

/* GUARDA EL ESTADO ACTUAL DEL USUARIO EN UNA VARIABLE */
const initialState = user
  ? { status: { loggedIn: true }, user, store_user } 
  : { status: { loggedIn: false }, user: null, store_user };
/* SI LA VARIBALE USUARIO NO ES VACIA, ESTA ACTVO
*  SI ESTA VACIO NO HAY USUARIO ACTIVO
*/

/* VARIABLE GLOBAL QUE TENDRA LOS DATOS DEL USUARIO ACTIVO ACTUAL */
const auth = {
  namespaced: true,
  state: initialState,
  actions: {
    /* ACCIONES DE AUTENTIFICACION */

    /* LOGIN: INICIAR SESIÓN */
    login({ commit }, user){
      /* DE LA CLASE 'AuthService' LLAMA LA FUNCIÓN DE LOGIN */
      return AuthService
        .login(user)
        .then(user => {

          /* SI INICIO CON ÉXITO, MUTA EL ESTADO DEL USUARIO A ACTIVO */
          commit('auth/loginSuccess', user);
          return Promise.resolve(user);

        })
    },

    /* CERRAR SESIÓN */
    logout({ commit }){
      /* DE LA CLASE 'AuthService' LLAMA LA FUNCIÓN DE LOGOUT */
      AuthService.logout();
      /* MUTA EL ESTADO DEL USUARIO A INACTIVO */
      commit('logout');
    },

  },

  mutations: {
    changeStore(state, store){
      state.store_user =  store;
    },

    loginSuccess(state, user){
      state.status.loggedIn = true;
      state.user = user;
      if(!state.user.is_staff)
        state.store_user = user.stores ? user.stores[0].store : [];
    },

    logout(state){
      state.status.loggedIn = false;
      state.user = null;
      state.store_user = null;
    }
  },
}

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    auth: auth
  }
})